@media only screen and (max-width: 480px) {
  .SplitPane {
    height: 900px;
    padding: 0 2% 0 2%;
    font-size: 25px;
    color: #3f3b3b;
    margin-bottom: 100px;
    display: flex;
    flex-direction: row;
  }
  .SplitPane img {
    height: 100px;
    width: 125px;
    margin: 1rem 1rem 10rem 0.5rem;
    /* background-color: rgb(104, 191, 146); */
  }

  .header {
    float: none;
    width: 100%;
  }

  .footer {
    float: none;
    width: 100%;
    position: fixed;
  }
}

